.buttons{
    display:flex;
}

.button{
    width:200px;
    height:75px;
    background:linear-gradient(90deg, #36a97f, #2abab8);
    font-size:24px;
    text-transform: uppercase;
    border:none;
    border-radius:8px;
    margin-bottom:10px;
    transition:all .1s linear;
    &:active, &:focus{
        outline:none;
        border:none;
    }
    &__active{
        box-shadow: inset -1px 6px 20px rgb(112, 107, 107);
        font-size:23px;
    }
}
li{
    list-style: none;
}
li.button{
    display:block;
    list-style: none;
    text-transform: none;
    text-align: center;
    line-height: 75px;
    font-size:12px;
}
.room-list{
    &__item{
        position: relative;
    }
}
.picking-item{
    cursor: pointer;
}

.subItem{
    display:none;    
    
    &.subItem__active{
        display:block;
        // position: relative;
        // left: 100%;
        // top: 100%;
        margin-left:200px;
    
    }
}
.room-list{
    display: none;
}
main{
    @extend .buttons;
    justify-content:space-between;
}
.result{
    width:300px;
    border:1px solid #ccc;
    box-shadow: 1px 3px 5px #ccc;
}